import { mapState } from "vuex";
import api from "./../../../../../utils/constants/ApiConstants";

export default {
    props: {},
    name: "media-from-computer",
    data() {
        return {
            isUploading: false,
            errorMessage: "",
            uploadPercentage: 0,
            files: [],
            fileUploaded: [],
            folder: "other",
            mediaType: 1,
        };
    },

    components: {

    },

    created() {

    },

    watch: {
        "$route.fullPath": function () {

        },
    },
    mounted() {

    },

    computed: {
        ...mapState(["mediaDirectory"]),
    },

    methods: {
        onChange() {
            this.files.unshift.apply(this.files, this.$refs.file.files)
        },
        async beforeUpload() {
            if (this.files.length === 0) {
                this.uploadPercentage = 0
                this.isUploading = false
                return
            }
            this.isUploading = true
            let response = await this.onUpload(this.files[0])
            if (response.status === 201) {
                await this.fileUploaded.push(this.files[0])
                await this.files.splice(0, 1)
                await this.beforeUpload()
            } else {
                this.$toast.error("Upload failed.");
            }
        },
        async onUpload(file) {
            console.log(file)
            let formData = new FormData();
            formData.append('file', file);

            let token = this.$cookies.get("accessToken");
            let params = "?folder=" + this.folder + "&type=" + this.mediaType
            return this.$axios.post(api.upload + params,
                formData, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                    Authorization: 'Bearer ' + token
                },
                onUploadProgress: function (progressEvent) {
                    this.uploadPercentage = parseInt(Math.round((progressEvent.loaded / progressEvent.total) * 100));
                }.bind(this)
            }
            ).then(function (response) {
                return response
            })
        },
        dragover(e) {
            e.preventDefault();
            this.isDragging = true;
        },
        dragleave() {
            this.isDragging = false;
        },
        drop(e) {
            e.preventDefault();
            this.$refs.file.files = e.dataTransfer.files;
            this.onChange();
            this.isDragging = false;
        },
        removeDraftFile(fileIndex) {
            this.files.splice(fileIndex, 1)
        },
        fileToPath(file) { return window.URL.createObjectURL(file) },
        resetBody() {
            this.uploadPercentage = 0
            this.files = []
            this.folder = "other"
            this.mediaType = 1
        }
    },
};
