import CustomerService from "@/utils/services/CustomerService"
import AgentListStatic from "@/static/agent.json"

export default {
    name: 'list-agent',
    data() {
        return {
            isLoading: false,
            tabActive: "pendingKYC",
            showFilter: true,
            agents: AgentListStatic,
            customers: [],
            search: {
                agentId: "",
                account: "",
                agentLevel: "",
                parentId: "",
                parentAccount: "",
            },
            agentLevel: [
                { val: "", label: "All Level" },
                { val: "1", label: "Level 1" },
                { val: "2", label: "Level 2" },
                { val: "3", label: "Level 3" },
                { val: "4", label: "Level 4" },
                { val: "5", label: "Level 5" },
                { val: "6", label: "Level 6" },
                { val: "7", label: "Level 7" },
                { val: "8", label: "Level 8" },
            ],
            customerStatusColor: {
                1: "badge-warning",
                2: "badge-success",
                3: "badge-danger",
                4: "badge-info",
                5: "badge-secondary"
            },
            pagination: {
                page: 1,
                limit: 10,
                totalPage: 1,
                length: 0
            },
        }
    },
    components: {

    },
    mounted() {

    },
    unmounted() {

    },
    created() {

    },
    watch: {
        "$route.fullPath": function () {

        }
    },
    methods: {
        onSearch(){
            let result = AgentListStatic

            if(this.search.agentId){
                result = result.filter(agent => agent.id.toString() == this.search.agentId)
            }

            if(this.search.account){
                result = result.filter(agent => agent.account.includes(this.search.account))
            }

            if(this.search.agentLevel){
                result = result.filter(agent => agent.agency_level.toString() == this.search.agentLevel)
            }

            if(this.search.parentId){
                result = result.filter(agent => agent.parent_id.toString() == this.search.parentId)
            }

            if(this.search.parentAccount){
                result = result.filter(agent => agent.parent_account.includes(this.search.parentAccount))
            }
            
            this.agents = result
        },

        sortByFieldName(fieldName){
            this.agents = this.$helper.sortByFieldName(fieldName, this.agents)
        },

        onReset(){
            this.search = {
                agentId: "",
                account: "",
                agentLevel: "",
                parentId: "",
                parentAccount: "",
            }

            this.onSearch()
        }
    }
}