import { mapState } from "vuex";
import UploadService from "./../../../../../utils/services/UploadService";

export default {
    props: {
        multiple: Boolean
    },
    name: "media-from-server",
    data() {
        return {
            files: [],
            listMedia: [],
            listSelectedMedia: [],
            folderSelected: "",
            pagination: {
                page: 0,
                limit: 20,
                length: 0
            }
        };
    },
    components: {

    },

    created() {

    },

    watch: {
        "$route.fullPath": function () {

        },
    },
    mounted() {

    },

    computed: {
        ...mapState(["mediaDirectory"]),
    },

    methods: {
        onClickFolder(folder) {
            this.folderSelected = folder
            this.listMedia = []
            this.getMedia()
        },

        getMedia() {
            let params = "?offset=" + this.pagination.page * this.pagination.limit + "&limit=" + this.pagination.limit + "&folder=" + this.folderSelected.code
            UploadService.getAllMedia(params).then((response) => {
                console.log("response", response)
                if (response?.data) {
                    this.listMedia.push.apply(this.listMedia, response.data);
                    this.pagination.length = response.meta.length
                }
            });
        },

        onSelectMedia(mediaIndex) {
            if (!this.multiple) {
                this.listSelectedMedia = [this.listMedia[mediaIndex].key]
            } else {
                this.listSelectedMedia.push(this.listMedia[mediaIndex].key)
            }
        },
        removeSelectedMedia(mediaIndex) {
            this.listSelectedMedia.splice(mediaIndex, 1)
        },
        clearSelectedMedia() {
            this.listSelectedMedia = []
        },
        submitSelectedMedia() {
            let media = !this.multiple ? this.listSelectedMedia[0] : this.listSelectedMedia
            this.$emit("selectedMedia", media)
            this.listSelectedMedia = []
        },
        showMore() {
            this.pagination.page += 1
            this.getMedia()
        }
    },
};
