import MainService from "@/utils/services/MainService";
import { mapState } from "vuex";
import Sidebar from "./../sidebar"
import AuthService from "@/utils/services/AuthService";

export default {
    name: 'navbar',
    emits: ['displaySidebar'],
    data() {
        return {
            isDropdownMenu: false,
            keySearch: "",
            originalMenuList: [],
            menuList: [],
        }
    },
    components: {
        Sidebar
    },
    created() {
        this.orderMenu()
    },
    mounted() { },
    computed: {
        ...mapState(["menus", "user"]),
    },
    methods: {
        orderMenu() {
            let result = []
            for (let i = 0; i < this.menus.length; i++) {
                let menu = this.menus[i]
                if (menu.childs[0]) {
                    result.push.apply(result, menu.childs)
                } else {
                    result.push(menu)
                }
            }
            this.originalMenuList = result
        },

        displaySidebar(type) {
            this.$emit("displaySidebar", type)
        },

        onSearchMenu() {
            this.menuList = this.originalMenuList.filter(item => item.label.toLowerCase().includes(this.keySearch.toLowerCase()))
        },

        hideDropdownMenu() {
            this.isDropdownMenu = false
        },

        routeTo(routeName) {
            this.$router.push({ name: routeName })
        },

        onLogOut() {
            AuthService.logout().then(response => {
                if (response.isExpiredToken) {
                    return AuthService.refreshToken().then((response) => {
                        if (response === "OK") { this.onLogOut() }
                    });
                }
                if (!response.success) {
                    this.errorMessage = response.error.message
                    return
                }

                // success
                MainService.logout()
            })
        }
    }
}