import AuthService from "@/utils/services/AuthService";
import CustomerService from "@/utils/services/CustomerService";
import Pool from "./components/pool"
import BidHistory from "./components/bid-history"
import Payment from "./components/payment"
import Settlement from "./components/settlement"
import PoolRequest from "./components/pool-request"
import Guarantor from "./components/guarantor"
import Guarantee from "./components/guarantee"

export default {
  name: "detail-agent",
  data() {
    return {
      isLoading: false,
      tabActive: "efundPool",
      customer: "",
      customerStatusColor: {
        1: "badge-warning",
        2: "badge-success",
        3: "badge-danger",
        4: "badge-info",
        5: "badge-secondary"
      },
      statuses: [
        { val: "efundPool", label: "eFund Pool" },
        { val: "bidHistory", label: "Bid History" },
        { val: "paymentHistory", label: "Payment History" },
        { val: "settlementHistory", label: "Settlement History" },
        { val: "guarantor", label: "Guarantor" },
        { val: "guaranteeFor", label: "Guarantee For" },
        { val: "requestPool", label: "Request Pool" }
      ],
      pagination: {
        page: 1,
        limit: 10,
        totalPage: 1,
        length: 0
      },
    };
  },
  components: {
    Pool,
    BidHistory,
    Payment,
    Settlement,
    PoolRequest,
    Guarantor,
    Guarantee
  },
  mounted() { },
  unmounted() { },
  created() {
    this.getDetail()
  },
  watch: {
    "$route.fullPath": function () {
      this.getDetail()
    }
  },
  methods: {
    getDetail() {
      let id = this.$route.params.id
      this.isLoading = true;
      CustomerService.detail(id).then((response) => {
        this.isLoading = false;
        if (response.isExpiredToken) {
          return AuthService.refreshToken().then((response) => {
            if (response === "OK") {
              this.getDetail();
            }
          });
        }

        if (!response.success) {
          return;
        }

        //success
        this.customer = response?.data
      });
    },
    onTabChange(tab) {
      this.tabActive = tab;
    },
    switchTab(tabName) {
      this.$root.$router.push({
        query: Object.assign({}, this.$route.query, {
          page: 1,
          limit: this.pagination.limit
        })
      })
      this.tabActive = tabName
    }
  },
};
