import MenuSidebar from './components/menu-sidebar'

export default {
    name: 'sidebar',
    emits: ['displaySidebar'],
    data() {
        return {}
    },
    components: {
        MenuSidebar
    },
    mounted() {
        this.getDimensions()
    },
    created() { },
    methods: {
        getDimensions() {
            let windowWidth = window.innerWidth
            if (windowWidth <= 1024) {
                this.displaySidebar('hide')
            }
        },

        displaySidebar(type) {
            this.$emit("displaySidebar", type)
        },
    }
}