import { createApp } from "vue";
import {
  createWebHistory,
  createWebHashHistory,
  createRouter,
} from "vue-router";

import PageNotFound from "@/views/page-not-found";
import Login from "@/views/login";
import Dashboard from "@/views/dashboard";

import ListService from "@/views/service/list";

import ListProduct from "@/views/product/list";

import ListUser from "@/views/user/list";

import ListOrder from "@/views/order/list";

import ListAgent from "@/views/agent/list";
import DetailAgent from "@/views/agent/detail";

import ListMember from "@/views/member/list";

import ListCustomer from "@/views/customer/list";

const needLogin = (to, from, next) => {
  if (!$cookies.get("accessToken")) {
    return next({ name: "login" });
  }
  return next();
};

const noNeedLogin = (to, from, next) => {
  if ($cookies.get("accessToken")) {
    return next({ name: "dashboard" });
  }
  return next();
};

const routes = [
  {
    path: "/login",
    name: "login",
    component: Login,
    beforeEnter: noNeedLogin,
  },
  {
    path: "/",
    name: "dashboard",
    component: Dashboard,
    beforeEnter: noNeedLogin,
  },
  {
    path: "/service",
    beforeEnter: noNeedLogin,
    children: [
      {
        path: "list",
        name: "list-service",
        component: ListService,
      }
    ],
  },
  {
    path: "/product",
    beforeEnter: noNeedLogin,
    children: [
      {
        path: "list",
        name: "list-product",
        component: ListProduct,
      }
    ],
  },
  {
    path: "/order",
    beforeEnter: noNeedLogin,
    children: [
      {
        path: "list",
        name: "list-order",
        component: ListOrder,
      }
    ],
  },
  {
    path: "/agent",
    beforeEnter: noNeedLogin,
    children: [
      {
        path: "list",
        name: "list-agent",
        component: ListAgent,
      },
      {
        path: "detail/:id",
        name: "detail-agent",
        component: DetailAgent,
      },
    ],
  },
  {
    path: "/member",
    beforeEnter: noNeedLogin,
    children: [
      {
        path: "list",
        name: "list-member",
        component: ListMember,
      }
    ],
  },
  {
    path: "/customer",
    beforeEnter: noNeedLogin,
    children: [
      {
        path: "list",
        name: "list-customer",
        component: ListCustomer,
      }
    ],
  },
  {
    path: "/user",
    beforeEnter: noNeedLogin,
    children: [
      {
        path: "list",
        name: "list-user",
        component: ListUser,
      }
    ],
  },
  { path: "/:pathMatch(.*)*", component: PageNotFound },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
