import AuthService from "@/utils/services/AuthService"
import { v4 as uuidv4 } from 'uuid';

export default {
    name: 'login',
    data() {
        return {
            isSubmitted: false,
            isSubmitting: false,
            errorMessage: "",
            showPassword: false,
            body: {
                username: "Super-Admin",
                password: "Admin@eFund12345!",
                deviceToken: "WEB",
                uuid: uuidv4()
            }
        }
    },
    components: {

    },
    mounted() {

    },
    unmounted() {

    },
    created() {

    },
    methods: {
        onLogin() {
            this.isSubmitted = true
            let validationMsg = this.validateLoginBody()
            if (validationMsg != "OK") { return }
            this.isSubmitting = true
            let body = {
                username: this.body.username,
                password: this.body.password,
                deviceToken: this.body.deviceToken,
                uuid: uuidv4()
            }
            AuthService.login(body).then(response => {
                this.isSubmitting = false
                if (!response.success) {
                    this.errorMessage = response.error.message
                    return
                }
                console.log(this)
                this.$cookies.set('accessToken', response.data.accessToken)
                this.$cookies.set('refreshToken', response.data.refreshToken)
                location.reload()
            })
        },

        validateLoginBody() {
            let body = this.body
            if (!body.username) { return "Username is required." }
            if (!body.password) { return "Password is required." }
            return "OK"
        }
    }
}