<template>
  <div class="app-container">
    <div class="row">
      <Sidebar v-show="showSidebar" @displaySidebar="displaySidebarCallback" />
      <div class="wrapper-container">
        <Navbar @displaySidebar="displaySidebarCallback" />
        <div class="main-wrapper p16">
          <router-view></router-view>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import Navbar from "@/components/navbar";
import Sidebar from "@/components/sidebar";
import UploadService from "./utils/services/UploadService";
import AuthService from "./utils/services/AuthService";

export default {
  name: "App",
  data() {
    return {
      isLoading: false,
      showSidebar: true,
    };
  },
  components: {
    Navbar,
    Sidebar,
  },
  created() {
    this.getProfile();
  },
  computed: {
    ...mapState(["tooManyRequested"]),
  },
  methods: {
    getProfile() {
      this.isLoading = true;
      let token = this.$cookies.get("accessToken");
      let refreshToken = this.$cookies.get("refreshToken");

      if (!token || !refreshToken) {
        return;
      }

      AuthService.profile().then((response) => {
        if (response.isExpiredToken) {
          return AuthService.refreshToken().then((response) => {
            if (response === "OK") {
              this.getProfile();
            }
          });
        }
        if (!response.success) {
          return;
        }
        // success
        this.getMediaDirectory();
        this.$store.commit("STORE_PROFILE", response.data);

        this.isLoading = false;
      });
    },
    getMediaDirectory() {
      UploadService.directory().then((response) => {
        if (response.isExpiredToken) {
          return AuthService.refreshToken().then((response) => {
            if (response === "OK") {
              this.getMediaDirectory();
            }
          });
        }
        if (!response.success) {
          return;
        }

        //success
        this.$store.commit("STORE_MEDIA_DIRECTORY", response.data);
      });
    },

    displaySidebarCallback(type) {
      this.showSidebar = type == "hide" ? false : true;
    },
  },
};
</script>

