import axios from "axios";
import MainService from "./MainService";
import ApiConstant from "../constants/ApiConstants";

const PendingKYCService = {};

PendingKYCService.list = async function (params = "") {
    return await axios
        .get(ApiConstant.listPendingKYC + params, MainService.headers())
        .then((response) => {
            return MainService.validateResponse(response);
        })
        .catch((error) => {
            return MainService.validateError(error);
        });
};

PendingKYCService.detail = async function (pendingId) {
    return await axios
        .get(ApiConstant.detailPendingKYC + "/" + pendingId, MainService.headers())
        .then((response) => {
            return MainService.validateResponse(response);
        })
        .catch((error) => {
            return MainService.validateError(error);
        });
};

PendingKYCService.verify = async function (pendingId) {
    return await axios
        .post(ApiConstant.verifyKYC + "/" + pendingId, {}, MainService.headers())
        .then((response) => {
            return MainService.validateResponse(response);
        })
        .catch((error) => {
            return MainService.validateError(error);
        });
};

export default PendingKYCService;
