import AuthService from "@/utils/services/AuthService";
import CustomerService from "@/utils/services/CustomerService";
import ModalDetail from "./components/modal-detail"

export default {
    name: "detail-pool",
    data() {
        return {
            isLoading: true,
            detailIndex: -1,
            pools: [],
            pagination: {
                page: 1,
                limit: 10,
                totalPage: 1,
                length: 0
            },
        };
    },
    components: {
        ModalDetail
    },
    mounted() { },
    unmounted() { },
    created() {
        this.getList()
    },
    methods: {
        getList() {
            this.isLoading = true
            this.pagination.page = this.$route.query.page || this.pagination.page
            this.pagination.limit = this.$route.query.limit || this.pagination.limit
            let customerId = this.$route.params.id
            let params = this.$helper.setParams(this.search, this.$route.query, this.pagination)
            CustomerService.poolList(customerId, params).then(response => {
                this.isLoading = false
                if (response.isExpiredToken) {
                    return AuthService.refreshToken().then((response) => {
                        if (response === "OK") { this.getList() }
                    });
                }
                if (!response.success) { return }

                // success
                this.pools = response.data
                this.pagination = response.meta
            })
        }
    },
};
