import CustomerService from "@/utils/services/CustomerService"
import MemberListStatic from "@/static/member.json"

export default {
    name: 'list-member',
    data() {
        return {
            isLoading: false,
            showFilter: true,
            members: MemberListStatic,
            search: {
                memberId: "",
                account: "",
                parentAccount: "",
            },
            pagination: {
                page: 1,
                limit: 10,
                totalPage: 1,
                length: 0
            },
        }
    },
    components: {

    },
    mounted() {

    },
    unmounted() {

    },
    created() {

    },
    watch: {
        "$route.fullPath": function () {

        }
    },
    methods: {
        onSearch(){
            let result = MemberListStatic

            if(this.search.memberId){
                result = result.filter(member => member.id.toString() == this.search.memberId)
            }

            if(this.search.account){
                result = result.filter(member => member.account.includes(this.search.account))
            }
            if(this.search.parentAccount){
                result = result.filter(member => member.parent_account.includes(this.search.parentAccount))
            }
            
            this.members = result
        },

        sortByFieldName(fieldName){
            this.members = this.$helper.sortByFieldName(fieldName, this.members)
        },

        onReset(){
            this.search = {
                memberId: "",
                account: "",
                parentAccount: "",
            }

            this.onSearch()
        }
    }
}