import axios from "axios";
import MainService from "./MainService";
import ApiConstant from "../constants/ApiConstants";

const UploadService = {}

UploadService.directory = async function () {
    return await axios
        .get(ApiConstant.mediaFolder, MainService.headers())
        .then(response => {
            return MainService.validateResponse(response)
        })
        .catch(error => {
            return MainService.validateError(error)
        })
}

UploadService.getAllMedia = async function (param) {
    return await axios
        .get(ApiConstant.mediaList + param, MainService.headers())
        .then(response => {
            return MainService.validateResponse(response)
        })
        .catch(error => {
            return MainService.validateError(error)
        })
}

UploadService.addMedia = async function (body) {
    return await axios
        .post(ApiConstant.mediaList, body, MainService.headers())
        .then(response => {
            return MainService.validateResponse(response)
        })
        .catch(error => {
            return MainService.validateError(error)
        })
}

UploadService.deleteMedia = async function (id) {
    return await axios
        .delete(ApiConstant.mediaList + '/' + id, MainService.headers())
        .then(response => {
            return MainService.validateResponse(response)
        })
        .catch(error => {
            return MainService.validateError(error)
        })
}

UploadService.uploadMedia = async function (uploadUrl, body, fileType) {
    return await axios
        .put(uploadUrl, body, {
            headers: {
                'Content-Type': fileType,
            },
        })
        .then(() => {
            return 'ok'
        })
        .catch(function (error) {
            return MainService.validateError(error)
        })
}

UploadService.uploadPresign = async function (body) {
    return await axios
        .post(ApiConstant.uploadPresign, body, MainService.headers())
        .then(response => {
            return MainService.validateResponse(response)
        })
        .catch(error => {
            return MainService.validateError(error)
        })
}

export default UploadService