import AuthService from "@/utils/services/AuthService";
import PendingKYCService from "@/utils/services/PendingKYCService";
import PoolService from "@/utils/services/PoolService";

export default {
  name: "modal-detail",
  props: {
    id: { type: Number, default: "" }
  },
  emits: ["onClose", "onSubmit"],
  data() {
    return {
      isLoading: false,
      isSubmitting: false,
      pool: "",
      poolStatusColor: {
        1: "badge-info",
        2: "badge-success",
        3: "badge-danger",
      }
    };
  },
  components: {},
  mounted() { },
  unmounted() { },
  created() {
    this.getDetail()
  },
  methods: {
    getDetail() {
      this.isLoading = true;
      PoolService.detail(this.id).then((response) => {
        this.isLoading = false;
        if (response.isExpiredToken) {
          return AuthService.refreshToken().then((response) => {
            if (response === "OK") {
              this.getDetail();
            }
          });
        }
        if (!response.success) {
          return;
        }

        //success
        this.pool = response?.data
      });
    },

    onSubmit() {
      this.isSubmitting = true
      PendingKYCService.verify(this.id).then(response => {
        this.isSubmitting = false
        if (response.isExpiredToken) {
          return AuthService.refreshToken().then((response) => {
            if (response === "OK") { this.onSubmit() }
          });
        }
        if (!response.success) {
          this.errorMessage = response.error.message
          return
        }

        //success
        this.$emit("onSubmit")
      })
    },

    onClose() {
      this.$emit('onClose')
    }
  },
};
