import axios from "axios";
import MainService from "./MainService";
import ApiConstant from "../constants/ApiConstants";

const CustomerService = {};

CustomerService.list = async function (params = "") {
    return await axios
        .get(ApiConstant.listCustomer + params, MainService.headers())
        .then((response) => {
            return MainService.validateResponse(response);
        })
        .catch((error) => {
            return MainService.validateError(error);
        });
};

CustomerService.detail = async function (customerId) {
    return await axios
        .get(ApiConstant.detailCustomer + "/" + customerId, MainService.headers())
        .then((response) => {
            return MainService.validateResponse(response);
        })
        .catch((error) => {
            return MainService.validateError(error);
        });
};

