import axios from "axios";
import MainService from "./MainService";
import ApiConstant from "../constants/ApiConstants";

const PoolService = {};

PoolService.create = async function (body) {
  return await axios
    .post(ApiConstant.createPool, body, MainService.headers())
    .then((response) => {
      return MainService.validateResponse(response);
    })
    .catch((error) => {
      return MainService.validateError(error);
    });
};

PoolService.list = async function (params = "") {
  return await axios
    .get(ApiConstant.listPool + params, MainService.headers())
    .then((response) => {
      return MainService.validateResponse(response);
    })
    .catch((error) => {
      return MainService.validateError(error);
    });
};

