export default {
    name: 'menu-sidebar',
    data() {
        return {
            menus: this.$store.state.menus,
            activeMenu: {
                parentName: "",
                childName: ""
            }
        }
    },
    created() {
        this.checkMenuActive()
    },
    watch: {
        "$route.fullPath": function () {
            this.checkMenuActive()
        }
    },
    methods: {
        checkMenuActive() {
            let path = this.$root.$route.path.substring(1)
            this.activeMenu.parentName = path.split('/')[0] || 'dashboard'
            this.activeMenu.childName = path.split('/')[1] ? path.split('/')[1] : ""

            this.menus.forEach(menu => {
                if (this.activeMenu.parentName == menu.val) {
                    menu.isDropped = true
                }
            });
        },

        onClickMenu(menu) {
            if (menu.childs.length > 0) {
                menu.isDropped = !menu.isDropped
            } else {
                this.navigateTo(menu.routeName)
            }
        },

        navigateTo(routeName) {
            this.$router.push({ name: routeName })
        },

        onLogOut() {
            this.navigateTo('login')
        },
    }
}