import MemberListStatic from "@/static/member.json"
import ModalConfirm from "@/components/shared/modal-confirm"

export default {
    name: 'list-user',
    data() {
        return {
            isLoading: false,
            showFilter: true,
            showCreateModal: false,
            showUpdateModal: false,
            showConfirmModal: false,
            members: MemberListStatic,
            search: {
                serviceId: "",
                name: "",
                status: "",
            },
            statusOptions: [
                { val: "", label: "All Status" },
                { val: "1", label: "Active" },
                { val: "2", label: "Inactive" },
            ],
            pagination: {
                page: 1,
                limit: 10,
                totalPage: 1,
                length: 0
            },
        }
    },
    components: {
        ModalConfirm
    },
    mounted() {

    },
    unmounted() {

    },
    created() {

    },
    watch: {
        "$route.fullPath": function () {

        }
    },
    methods: {
        onSearch(){
            let result = MemberListStatic

            if(this.search.memberId){
                result = result.filter(member => member.id.toString() == this.search.memberId)
            }

            if(this.search.account){
                result = result.filter(member => member.account.includes(this.search.account))
            }
            if(this.search.parentAccount){
                result = result.filter(member => member.parent_account.includes(this.search.parentAccount))
            }
            
            this.members = result
        },

        sortByFieldName(fieldName){
            this.members = this.$helper.sortByFieldName(fieldName, this.members)
        },

        onReset(){
            this.search = {
                memberId: "",
                account: "",
                parentAccount: "",
            }

            this.onSearch()
        },

        closeCreateModal(){
            this.showCreateModal = false
        },

        closeUpdateModal(){
            this.showUpdateModal = false
        }
    }
}